@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
* {
  background-color: #ffffe6;
  margin: 0;
  font-family: 'Kaushan Script', cursive;
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid teal;
}
.navbar h1 {
  color: teal;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* Blogs */
.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.3);
}
.blog-preview h2 {
  font-size: 20px;
  color: #cc0000;
  margin-bottom: 8px;
}
